body {
    background: rgb(236, 236, 239);
    background: radial-gradient(
      circle,
      rgb(238, 238, 245) 0%,
      rgb(237, 244, 245) 0%,
      rgba(93, 93, 116, 1) 100%
    );
  }
  .input{
    margin-left:5px ;
    border: 2px solid rgb(14, 0, 0);
    border-radius: 5px;
    height: 20px;

  }
  .field{
    margin: 5px;
    font-size: larger;
    font-weight:500;
    font-style: italic;
  }
 .container{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
 }
  .container >form {
    width: 45%;
    height:fit-content;
    border: 1px solid #d9c9c9;
    justify-content: center;
    padding: 20px;
    border-radius: 5px;
    background: #c99999;
    font-size: large;
  }

  pre {
    width: 70%;
    color: #fff;
    font-size: larger;
  }
  
  button {
    width: fit-content;
    margin: 40px;
    height: 40px;
    border-radius: 5px;
    background: #c8d2db !important;
  }
  
  p {
    color: red;
  }
  .navbar{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    
  }
  .courses{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    flex-direction: row;
    flex-wrap: wrap;

  }
  .course{
    width: 230px;
    margin: 10px;
    height: 300px;
    border: 2px solid ghostwhite;
    border-radius: 5px;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row-reverse;
  }

 